.footer{width:100%;padding:1rem 3rem;height:var(--footer-height);background-color:#fff;color:#000}.footer .container{margin:0 auto;display:flex;justify-content:space-between;align-items:center}.footer .leftContainer,.footer .rightContainer{display:flex;gap:1rem;align-items:center}.footer .link{font-family:var(--font-geist-sans);font-size:1rem;font-weight:400;text-decoration:none;padding:.5rem 1rem;border:.1rem solid #000;border-radius:2rem}.footer .link:hover{background-color:#000;color:#fff}